@import '../../assets/sass/main.scss';


.contact {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;

    .getInTouch {
        align-items: center;
        margin: 0px 0px 0px 50px;
        // border: 1px solid black;
        padding: 5px 5px 5px 5px;

        h4 {
            font-family: Inter;
        }

        p {
            margin-top: 10px;
            font-family: Inter;
            color: #666;
            font-family: Inter;
            font-size: 13px;
            font-style: normal;
            font-weight: 600;
            line-height: 24px;
            /* 133.333% */
            letter-spacing: 0.36px;
        }

        h6 {
            margin-top: 10px;
            font-family: Inter;
            font-size: 16px;
            font-weight: 600;
        }

        .map {
            // width:100%;
            height: 50%;
            @include breakpoint(sm) {
                display: none;
                }
        }

    }

    .dataForm {
        background: #F9F9F9;
    }
}