// @import "../../../assets/sass/main.scss";
.containerAllMachinery {
    display: flex;
    flex-direction: column;
    background-color: #f5f5f5;
    width: 100%;
    height: 584px;


    @media screen and (max-width: 500px) {
        height: 430px;
    }
   
    .oneStopSourceFor1 {
        padding-top: 40px;
        font-size: 32px;
        text-transform: capitalize;
        font-family: Inter;
        color: #363636;
        text-align: center;
        margin-bottom: 10px;

        @media screen and (max-width: 500px) {
            font-size: 20px; 
            line-height: 5px;
        }
        }

        .cardContainer{
            margin: 0;
            padding: 0;
          display: flex;
          flex-direction: row;

         
         justify-content: center;
         flex-wrap:wrap;
          gap: 30px;
         width: 100%;
         @media screen and (max-width: 500px) {
            gap: 10px;
            padding-top: 25px;
            }

        .rectangleParent {
        position: relative;
        padding-top: 25px;
        // width: %;
        // height: 230px;
        text-align: center;
        font-size: 20px;
        color: #363636;
        font-family: Inter;
        
        @media screen and (max-width: 500px) {
            font-size: 16px;
            padding-top: 0px;
            }
        

        .instanceChild {
            padding-top: 40px;
            top: 0px;
            left: 0px;
            border-radius: 4px;
            background-color: #fff;
            width: 200px;
            height: 200px;

            @media screen and (max-width: 500px) {
                width: 100px;
                height: 100px;
                padding-top: 10px;
                }
            .title{
               padding: 20px; 
               @media screen and (max-width: 500px) {
                padding: 0;
                font-size: 12px;
             
                }
            }
            .maskGroupIcon { 
                width: 72px;
                height: 72px;
                object-fit: cover;

                @media screen and (max-width: 500px) {
                width: 30px;
                height: 30px;
                }
                }
              
            }
        }
        
        }

    }
    
 
        

    